import React, { PureComponent } from 'react';

class RedSquareTitle extends PureComponent {
  render() {
    return (
      <div style={{
        background: "#e60013",
        top: 266,
        left: 128,
        right: 100,
        height: 3,
        ...this.props.style
      }}>
        <div style={{
          bottom: 3,
          left: 0,
          width: 40,
          height: 40,
          background: '#e60013'
        }} />
        <div style={{
          bottom: 10,
          left: 50,
          fontSize: 24.5,
          fontWeight: 'bold'
        }}>{this.props.children}</div>
      </div>
    );
  }
}

export default RedSquareTitle;