import React, { PureComponent } from 'react';

class REPlusRedLogo extends PureComponent {
  render() {
    return (
      <div style={{
        top: 106,
        left: 131,
        right: 0,
        height: 90,
        ...this.props.style
      }}>
        <div style={{
          top: 0,
          left: 0,
          width: 90,
          height: 90,
          background: "#e60013",
          color: '#ffffff',
          whiteSpace: 'nowrap'
        }}>
          <div style={{ fontSize: 10, top: 12, left: 4, right: 4, textAlign: 'center', letterSpacing: 6.4 }}>再利用資材</div>
          <div style={{
            fontSize: 39,
            fontWeight: 'bold',
            top: 26,
            left: 3,
            letterSpacing: 4
          }}>re+</div>
          <div style={{ fontSize: 10, fontWeight: 'bold', top: 70, left: 4 }}>レプラスシリーズ</div>

        </div>
        <div style={{
          top: '55%',
          left: 110,
          transform: 'translate(0, -50%)'
        }}>{this.props.children}
        </div>
      </div>
    );
  }
}

export default REPlusRedLogo;